* {
  box-sizing: border-box;
}

::-webkit-scrollbar {
  height: 15px;
  width: 5px;
}

::-webkit-scrollbar-track {
  background-color: #0000;
}

::-webkit-scrollbar-thumb {
  background-color: var(--all-categories-scroll-color);
  border-radius: 15px;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.theme-switch__input:checked ~ main, .theme-switch__input:checked ~ label {
  --text: white;
}

.theme-switch__input:checked ~ main {
  --gradDark: #102128;
  --gradLight: #2d5567;
}

.burger-menu-icon {
  fill: var(--icons-color);
  width: 28px;
  height: 28px;
}

.theme-switch__input {
  opacity: 0;
}

.theme-switch__label {
  display: block;
  position: relative;
}

.theme-switch__input {
  width: 40px;
  z-index: 10;
  position: absolute;
}

.theme-switch__label {
  width: 40px;
  height: 20px;
  text-align: center;
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 50px;
  transition: background-color .2s ease-in-out;
  box-shadow: inset 1px 1px 2px #0000001a;
}

.theme-switch__label:before, .theme-switch__label:after {
  font-size: 2rem;
  position: absolute;
  top: 50%;
  transform: translate3d(0, -50%, 0);
}

.switcher {
  width: 100%;
  position: absolute;
  bottom: calc(100% + 10px);
  left: 0;
}

.switcher:after {
  content: "";
  width: 18px;
  height: 18px;
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  transition: transform .2s, background-color .2s;
  position: absolute;
  top: calc(100% + 11px);
  left: 1px;
  transform: translate(0);
  box-shadow: 1px 1px 2px #0000001a, 0 2px 2px #0000001a, 1px 2px 4px #0000001a;
}

.theme-switch__input:checked ~ .theme-switch__label {
  background-color: #789;
}

.theme-switch__input:checked ~ .theme-switch__label:after {
  color: #40e0d0;
}

.theme-switch__input:checked ~ .theme-switch__label .switcher:after {
  transform: translate(20px);
}

